import { Component } from '@angular/core';
import { NavController , AlertController, NavParams, ActionSheetController, LoadingController  } from 'ionic-angular';
import {Camera, CameraOptions} from '@ionic-native/camera';
import {Http,Headers, RequestOptions} from '@angular/http';
import { OcrInputPage } from '../ocr-input/ocr-input';
import { PdfPage } from '../pdf/pdf';
import { ocr } from '../../interfaces/ocr';
let headers = new Headers({ 'Content-Type': 'application/json' });
let options = new RequestOptions({ headers: headers });


let ocrUrl =  "http://nebula.nubeslab.com:8080/document";
@Component({
  selector: 'page-home-camera',
  templateUrl: 'home-camera.html'
})
export class HomeCameraPage {
  public photos : any;
  public base64Image : string;
  OCRAD :any;
  value :any;
  srcImage: string;
  parameter:string;
  type:string;
  conf:string;
  
  ocr: ocr = { base64: '', conf: 'axh', ftype: 'jpeg', type: 'health' , companyName:''};
  
  constructor(public navCtrl : NavController, private camera : Camera, public navParams: NavParams, public actionSheetCtrl: ActionSheetController,
    public loadingCtrl: LoadingController, private alertCtrl : AlertController,private http: Http) {
     // this.ocr.type = navParams.get('param1');
     // this.companyName = navParams.get('param2');
     // this.ocr.conf = navParams.get('param3');
      //alert(this.ocr.conf);
      //alert(this.ocr.type);
    
}
ionViewDidLoad() {
  console.log('ionViewDidLoad EmiCalPage');

}
//allow them to choose from multiple photo options, 0 = library  1 = camera take pic
presentActionSheet() {
  const actionSheet = this.actionSheetCtrl.create({
    buttons: [
      {
        text: 'Choose Photo',
        handler: () => {
          this.getPicture(0); //0 == library 
        }
      },{
        text: 'Take Photo',
        handler: () => {
          this.getPicture(1); // 1 == Camera
        }
      },{
        text: 'Cancel',
        role: 'cancel'
      }
    ]
  });
  actionSheet.present();
}

  ngOnInit() {
    this.photos = [];
  }

  
  getPicture(sourceType: number) {
    const options : CameraOptions = {
      quality: 75, // picture quality
      sourceType,
     targetWidth: 500,
     targetHeight: 500,
     correctOrientation: true,
      destinationType: this.camera.DestinationType.DATA_URL,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: this.camera.MediaType.PICTURE
    }
   
    
    this.camera.getPicture(options) .then((imageData) => {
        this.base64Image = "data:image/jpeg;base64," + imageData;
        this.photos.push(this.base64Image);
        this.photos.reverse();
        alert(imageData);
        this.onClick(imageData);
       },(err) => {
        console.log(err);
      });
     
  }
 deletePhoto(index) {
    let confirm = this.alertCtrl.create({
        title: 'Sure you want to delete this photo? There is NO undo!',
        message: '',
        buttons: [
          {
            text: 'No',
            handler: () => {
              console.log('Disagree clicked');
            }
          }, {
            text: 'Yes',
            handler: () => {
              console.log('Agree clicked');
              this.photos.splice(index, 1);
            }
          }
        ]
      });
    confirm.present();
  }
  analyze() {
    let loader = this.loadingCtrl.create({
     content: 'Please wait...'
    });
    loader.present();
    (<any>window).OCRAD(document.getElementById('image'), text => {
      loader.dismissAll();
      alert(text);
      console.log(text);
    });
  }

  restart() {
    this.srcImage= '';
    this.presentActionSheet();
  }
  onPDFPage()
  {
  this.navCtrl.push(PdfPage);
  }
  
   onOCRPage()
    {
    this.navCtrl.push(OcrInputPage);
    }
 
    onClick(imageData:any) {
     // alert(imageData);
     
     let data = "data:image/jpeg;base64," + imageData;
     console.log(imageData);
      let postdata={
        base64:data,
        conf: this.ocr.conf,
        ftype: this.ocr.ftype,
        type:this.ocr.type
     };

      console.log(postdata);
	  
      this.http.post(ocrUrl, postdata,options).map(res => res.json()).subscribe(data => {
          alert("Enter");
          console.log(data);
            },
            err => {
              if(err){
            
              }
              console.log("ERROR!: ", err);
            }
        );
     
      
    }



}



